@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
    height: 100%;
    width: 100%;
    background-color: #fff;
    font-family: "Quicksand", sans-serif;
    overflow: hidden;
}

.q-container{
    height: 100vh;
    padding: 2%;
    background-image:linear-gradient(to bottom, rgb(116, 185, 255, 0.9), rgb(9, 132, 227, 1)),url('https://www.wallpapertip.com/wmimgs/124-1249648_police-hd.jpg') ;
    background-repeat: no-repeat;
    background-size: cover;
}

/* DEKSTOP */

@media (min-width: 1025px) {

    .question{

        padding: 20px;
        background-color: rgb(255, 255, 255, 0.3);
        border-radius: 5px;
        z-index: 200;
    }

    .question span {
        color: #fff;
        font-size: 20px;
        font-weight: bold;
    }

    .option{
        padding: 20px;
        background-color: rgb(255, 255, 255, 0.3);
        border-radius: 10px;
        height: 65%;
        margin-top: 20px;
        overflow: auto;
        z-index: 2;
    }

    /* width */
        .option::-webkit-scrollbar {
        width: 20px;
    }
    
    /* Track */
    .option::-webkit-scrollbar-track {
        box-shadow: inset 0 0 5px grey; 
        border-radius: 5px;
    }
    
    /* Handle */
    .option::-webkit-scrollbar-thumb {
        background: black; 
        border-radius: 5px;
    }
    
    /* Handle on hover */
    .option::-webkit-scrollbar-thumb:hover {
        background: #222f3e; 
    }

    .option div{
        color: #fff;
        padding: 20px;
        background-color: rgb(0, 0, 0, 0.5);
        border-radius: 1px;
        font-size: 18px;
        font-weight: bold;
        margin-bottom: 20px;
    }
    

    .footer{
        position: absolute;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(8vw, 1fr));
        grid-column-gap: 10px;
        -webkit-column-gap: 10px;
                column-gap: 10px;
        grid-row-gap: 15px;
        row-gap: 15px;
        margin-top: 2vh;
        transition: linear 0.5s;
        bottom: -300px;
    }

    .footer div{
        padding: 5px;
        margin-top: 5%;
    }

    .btn {
        width: 100%;
        aspect-ratio: 1;
        background-color: rgb(255, 255, 255, 0.3);
        padding: 15px;
        font-size: 18px;
        color: #fff;
        font-weight: bold;
        border-radius: 10px;
        border: 0px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: linear 0.1s;
    }

    .btn:hover{
        transform: scale(1.05);
        background-color: rgb(255, 255, 255, 0.6);
    }

    .btn img{
        width: 50%;
    }

    .btn span{
        font-size: 12px;
        margin-top: 10px;
    }

    .footer2{
        position: absolute;
        width: 20%;
        display: flex;
        justify-content: center;
        justify-content: flex-end;
        margin-top: 2vh;
        transition: linear 0.5s;
        bottom: 20px;
        right: 30px;
    }

    .footer2 div{
        padding: 5px;
        margin-top: 5%;
    }

    .btn2 {
        width: 95%;
        aspect-ratio: 1;
        background-color: rgb(255, 255, 255, 0.3);
        padding: 15px;
        font-size: 18px;
        color: #fff;
        font-weight: bold;
        border-radius: 10px;
        border: 0px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        transition: linear 0.1s;
    }

    .btn2:hover{
        transform: scale(1.05);
        background-color: rgb(255, 255, 255, 0.6);
    }

    .btn2 img{
        width: 50%;
    }

    .btn2 span{
        font-size: 12px;
        margin-top: 10px;
    }
}


